.Back {
    position: relative;
    border: 2px solid #ffffff;
    border-radius: 8px 0px 0px 8px;
    background-color: #e0e0e0;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    padding: 4px;
    box-sizing: border-box;
    cursor: pointer;
    } 

.Back:hover {
    cursor: pointer;
    color: white;
    box-shadow: inset 0px 0px 6px 1px rgba(0, 0, 0, 0.350);
    transition: 0.3s;
    background-color: #4fbc9c;
    /* border: 2px solid #111;  */
}

.Back:active {
    cursor: pointer;
    color: white;
    background-color: #3a3a3a;
    border: 2px solid #111; 
    transition: 0.3s;
}

.Alt {
    display: flex;
    height: 36px;
    padding: 8px;
    align-items: center;
    border: none;
    border-radius: 8px;
    gap: 8px;
    background-color: #515151;
    width: 100%;
    justify-content: space-between;
    color: #FBFBFB;
    text-align: center;
    font-family: Poppins;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
}

.Alt:hover {
    cursor: pointer;
    border-radius: 8px;
    background-color: #5a5a5a;
    border: 2px solid #111; 
    transition: 0.3s;
}

.Alt:Active {
    cursor: pointer;
    border-radius: 8px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.45);
    background-color: #111;
    border: 2px solid #111;
    transition: 0.3s;
}

.Alt-Ghost {
    position: relative;
    border-radius: 8px;
    border: 2px solid #515151;
    box-sizing: border-box;
    width: 100%;
    height: 36px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 16px;
    gap: 8px;
    text-align: center;
    font-size: 14px;
    font-weight: 700;
    color: #515151;
    font-family: Poppins;
}

.Alt-Ghost:hover {
    cursor: pointer;
    border-radius: 8px;
    color: white;
    background-color: #5a5a5a;
    border: 2px solid #111;
    transition: 0.3s; 
}

.Alt-Ghost:Active {
    cursor: pointer;
    border-radius: 8px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.45);
    background-color: #111;
    border: 2px solid #111;
    transition: 0.3s;
}

.Round {
    display: flex;
    height: auto;
    padding: 0px;
    align-items: center;
    border: none;
    border-radius: 999px;
    background: #4fbc9c;
    border: 2px solid #42a084;
    width: 100%;
