.CaseList {
    width: 100%;
    position: relative;
    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.06), -2px -2px 9px rgba(0, 0, 0, 0.06);
    border: none;
    border-radius: 8px;
    border: 2px solid #f8f8f8;
    background: linear-gradient(93.84deg, #fff, #f8f8f8);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    padding: 12px;
    box-sizing: border-box;
    gap: 4px;
    cursor: pointer;
    text-align: left;
    font-size: 16px;
    color: #007c89;
    font-family: Poppins;
}

.CaseList:hover {
    cursor: pointer;
    border: 2px solid #007c89;
    background: #f3f3f3;
    transition: 0.3s;   
}

.CaseList.active {
    box-shadow: 2px 2px 12px rgba(0, 0, 0, 0.75), -2px -2px 9px rgba(0, 0, 0, 0.06);
    background: linear-gradient(93.84deg, #fff, #f8f8f8);
    border-left: 4px solid #4fbc9c;
    box-sizing: border-box;
}

